<script>
    import {flyModified} from '../flyModified.js'
    import {onMount} from 'svelte'

    let results = 25
    let showing = 0
    let show = 'newsletters'
    let totalResultsPossible = 0

    export let getPageData
    let newsletters = []

    function getMoreNewsletters() {
        fetch('data/php/getNewsletters.php?start=' + showing + '&end=' + (showing + results) + '&direction=desc')
        .then(data=> data.json())
        .then(ns=> {
            newsletters = ns.results
            showing += results
            totalResultsPossible = ns.files
        })
    }

    onMount(()=> {
        getMoreNewsletters()
    })

    let bgImages = [
        "/primary-images/ns1.JPG",
        "/primary-images/ns2.JPG",
        "/primary-images/ns3.JPG"
    ]
</script>

<div class="page">
    <slot></slot>
    <div in:flyModified={{y: 300, duration: 300, delay: 500}} out:flyModified={{y: -300, duration: 300}}>
        <div class='content'>
            <h1>Newsletters</h1>

            {#if !newsletters.length}
                <div class='loader'>
                    <img src='/icons/loading.svg' alt='loading content'>
                </div>
            {:else}
                <div class="news-grid">
                    {#each newsletters.sort((a,b)=> new Date(b.match(/\d*\.\d*\.\d*(?=\.pdf)/i)).valueOf() - new Date(a.match(/\d*\.\d*\.\d*(?=\.pdf)/i)).valueOf()) as newsletter}
                        <a target="_blank" href={newsletter} class="newsletter">
                            <img src={bgImages[Math.floor(Math.random(bgImages.length) * 3)]} alt="">
                            <span>{(new Date(newsletter.slice(0, -4))).toDateString()}</span>
                        </a>
                    {/each}
                </div>
            {/if}

            <div class="centered mt2">
                {#if showing <= totalResultsPossible}
                    <a href="#moreNls" on:click|preventDefault={getMoreNewsletters}>More newsletters</a>
                {:else}
                    No more newsletters
                {/if}
            </div>
        </div>
    </div>
</div>

<style lang='scss'>.news-grid {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  -webkit-box-align: stretch;
      -ms-flex-align: stretch;
          align-items: stretch;
  -webkit-box-pack: stretch;
      -ms-flex-pack: stretch;
          justify-content: stretch;
  grid-gap: 1em;
  margin: 1em auto; }
  @media screen and (min-width: 500px) {
    .news-grid {
      -ms-grid-columns: 1fr 1fr 1fr;
      grid-template-columns: 1fr 1fr 1fr; } }
  @media screen and (min-width: 800px) {
    .news-grid {
      -ms-grid-columns: 1fr 1fr 1fr 1fr;
      grid-template-columns: 1fr 1fr 1fr 1fr; } }
  @media screen and (min-width: 1000px) {
    .news-grid {
      -ms-grid-columns: 1fr 1fr 1fr 1fr 1fr;
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr; } }

.newsletter {
  height: 10em;
  border-width: 2px;
  border-style: solid;
  border-color: #424242;
  position: relative;
  overflow: hidden; }
  .newsletter img {
    -o-object-fit: cover;
       object-fit: cover;
    min-width: 100%;
    height: 100%;
    margin: 0; }
  .newsletter span {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    text-align: center;
    padding: .5em;
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
    -webkit-box-sizing: border-box;
            box-sizing: border-box; }
  .newsletter:hover {
    border-color: #d40f41;
    -webkit-filter: brightness(1.2);
            filter: brightness(1.2);
    cursor: pointer;
    -webkit-box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.6);
            box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.6); }</style>